let app = getApp();
export default{
	data(){
		return{
			array:'',
			name:'',
			phone:'',
			region:'',
			minute:'',
		}
	},
	onLoad() {
		let _this = this
		uni.getStorage({
			key:"name",
			success(e){
				e.data
			}
		})
		uni.request({
			url:'http://ceshi3.dishait.cn/api/useraddresses/1',
			header:{
				token:"7dadcf0161710b5256265ed25cb7873b3fd61633"	
			},
			data:{
				page:1
			},
			success: (res) => {
				this.array = res.data.data
			}
		})
		console.log(app.globalData)
	},
	methods:{
		/**
		 * @returnClick:返回至设置页面
		 */
		returnClick(){
			uni.navigateTo({
				url:'../setting/setting'
			});
		},
		/**
		 * @addClick：跳转至新增地址页面
		 */
		addClick(){
			uni.navigateTo({
				url:'../added/added'
			});
		},
		/**
		 * @edit：编辑页面
		 */
		edit(s,index){
			let name = s.name
			let phone = s.phone
			let province = s.province
			let district = s.district
			let city = s.city
			let address =s.address
			let id =s.id
			let zip =s.zip
			// console.log(s,index)
			uni.navigateTo({
				url:`../edit/edit?name=${name}&phone=${phone}&province=${province}&district=${district}&city=${city}&id=${id}&address=${address}&zip=${zip}&index=${index}`
			})
		}
	}
}

